$danger: #8a0001;
$danger-hover: #640001;
$danger-active: #570001;
$success: #70ab81;
$warning: #eacb86;
$secondary: #fbf6eb;
$dark: #333333;
$input-placeholder-color: #aaaaaa;
$white: #ffffff;
$black: #000000;
$link-color: $white;
$body-color: $dark;

$input-height: 25px;
$box-shadow: 0 3px 7px 0;

// $font-size-base: 1rem; // Assumes the browser default, typically `16px`
$font-family-sans-serif: "Roboto Slab",
"Open Sans",
serif;
$font-size-base: 1rem;
$font-size-sm: $font-size-base * 0.5; // 8
$font-size-md: $font-size-base * 0.88; // 14
$font-size-lg: $font-size-base * 1.25; // 20

$font-size-10: $font-size-base * 0.625;
$font-size-12: $font-size-base * 0.75;
$font-size-18: $font-size-base * 1.13;

$display-5: $font-size-base * 1.38; // 22
$display-4: $font-size-base * 1.5; // 24
$display-3: $font-size-base * 1.63; // 26
$display-2: $font-size-base * 1.88; // 30

$navbar-toggler-padding-y: 0.1rem;
$navbar-toggler-padding-x: 0.4rem;
$navbar-dark-color: $white;
$navbar-dark-toggler-icon-bg: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'><path stroke='#{$navbar-dark-color}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");

$btn-border-radius: 0;
$input-border-radius: 0;
$input-border-radius-lg: 0;
$input-border-radius-sm: 0;
$border-width: 0;

$padding-value: calc(50% - var(--container-width) / 2);
$grid-breakpoints: (xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px);

